
import { Options, Vue, setup } from "vue-class-component";
import { useStore } from "vuex";
import { ref } from "vue";
import { NavBar, Icon, Popover, Empty, Button } from "vant";
import User from "@/components/user.vue";
import { formatSessionListTo } from "@/utils/im";
import { AllState } from "@/store";

@Options({
  components: {
    User,
    NavBar,
    Button,
    Icon,
    Popover,
    Empty
  }
})
export default class Home extends Vue {
  chat = setup(() => {
    const store = useStore<AllState>();
    const showPopover = ref(false);

    // 获取本地会话
    const getLocalConversation = () => {
      store.state.IM.connect.localCache.getLocalConversation( {
        conversationId: "yjj",
        conversationType: "singleChat"
      }).then((res: any) => {
        console.log(res, "ressss");
      });
    };
    // 设置本地会话自定义字段
    const setLocalConversationCustomField = () => {
      let opt = {
        conversationId: "yjj",
        conversationType: "singleChat",
        customField: "自定义字段"
      };
      store.commit("IM/setConversationCustomField", opt);
    };

    // 删除本地会话
    const removeLocalConversation = () => {
      let opt = {
        conversationId: "yjj",
        conversationType: "singleChat"
      };
      store.commit("IM/removeLocalConversation", opt);
    };

    // 清除会话未读数
    const clearConversationUnreadCount = () => {
      let opt = {
        conversationId: "yjj",
        conversationType: "singleChat"
      };
      store.commit("IM/clearConversationUnreadCount", opt);
    };

    const syncServerConversationListToLocal = () => {
      store.state.IM.connect.contact
        .getServerConversations({
          pageSize: 200,
          cursor: ""
        })
        .then((res) => {
          console.log(res, 'resssssss');
          setTimeout(() => {
            store.state.IM.connect.localCache.getLocalConversations().then((res: any) => {
              console.log("local conversations", res);
            });
          }, 100);
        })
        .catch((e) => {
          console.log(e);
        });
    };

    // 获取本地会话
    const getLocalConversations = () => {
      store.state.IM.connect.localCache.getLocalConversations().then((res: any) => {
        console.log("local conversations", res);
        store.commit("IM/setConversationList", { list: res.data });
      });
    };

    return {
      store: store.state.IM,
      showPopover,
      formatSessionListTo,
      getLocalConversation,
      setLocalConversationCustomField,
      removeLocalConversation,
      clearConversationUnreadCount,
      getLocalConversations,
      syncServerConversationListToLocal
    };
  });
}
